.route-grid{
    margin-top: 9rem;
    display: grid;
    grid-template-columns: 39.4rem repeat(2, 1fr);
    grid-template-rows: 57.8rem 1fr ;
    grid-template-areas: "side pic pic"
                          "side info map";

  @media(max-width:100em){
       margin-top: 8rem;
       grid-template-columns: 37.4rem repeat(2, 1fr);
       grid-template-rows: 50rem 1fr ;
   }

   @media(max-width:90em){
       margin-top: 7rem;
       grid-template-columns: 35rem repeat(2, 1fr);
       grid-template-rows: 40rem 1fr ;
   }
   @media(max-width:62em){
    grid-template-columns:  repeat(4, 1fr);
    grid-template-rows: auto;
    grid-template-areas: "side side side side "
                          "pic pic pic pic "
                          "info info map map";
   }
   @media(max-width:47em){
  grid-template-rows: 1fr;
    grid-template-areas: "side"
    "pic"
    "info"
    "map";
    margin-top: 6rem;

    
   }

    &__side{
        background-color:var(--color-drak-blue);
        color: var(--color-white);
        grid-area: side;
        padding: 4rem;
        @media(max-width:100em){
            padding: 3rem;
        }
        @media(max-width:62em){
           grid-column: 1/ -1;
           }
           @media(max-width:47em){
               padding: 15px;
           }

        .back-link{
            display: flex;
            align-items: center;
            text-transform: uppercase;
        }

        span{
            color: var(--color-orange);
            font-size: 1.6rem;
        }
        .icon-back{
            width: .7rem;
            height: 1.4rem;
            stroke: var(--color-orange);
            margin-inline-end: .7rem;
        }
    }

    .place-list-wrap{
        @media(max-width:62em){
           width: 100%;
           overflow-x: auto;
        }
    }
    
    .place-list{
        @media(max-width:62em){
           min-width: max-content;
            }

            .place-item{
                @media(max-width:62em){
                    display: inline-block;
                    &:not(:last-child){
                        margin-inline-end: 1.6rem;
                    }
                }
            }
    }
    .selected-place-item{
        filter: saturate(1) !important;
    }

   

    &__picture{
        grid-area: pic;
        overflow: hidden;
        @media(max-width:47em){
            grid-column: 1/ -1;
        }
        img{
            @extend%object-fit;
        }

        .slick-slider, .slick-track, .slick-list{
            height: 100% !important;
            overflow: hidden;
        }
        .slick-slider img{
            height: 100%; 
            width: 100%; 
            object-fit: contain;
        }

    }

    &__info{
        grid-area: info;
        padding: 3rem;
        text-align: start;
        height: 90vh ;
        overflow-y: auto;
        @media(max-width:47em){
            width: 100%;
            grid-column: 1/ -1;
            height: auto ;
            padding: 15px;
        }

        &__head{
            display: flex;
            justify-content: space-between;
            align-items: center;
            h4{
                font-size: 3.6rem;
                font-family: var(--font-medium);
                @media(max-width:100em){
                    font-size: 3rem;
                }
               
            }
        }

        &__title-sub{
            font-size:2rem;
            font-family: var(--font-regular);
            color: #808080;
            display: block;
            margin-top: 1.1rem;
            @media(max-width:100em){
                font-size: 1.8rem;
            }
            @media(max-width:90em){
                font-size: 1.6rem;
            }
        }

        &__icons{
            .favourite-btn{
                line-height: 0;
                svg{
                    width: 2rem;
                    height: 2rem;
                }
            }
            li{
                width: 4.4rem;
                height: 4.4rem;
                border-radius: 2rem;
                background-color: var(--color-light-blue);
                color: var(--color-white);
                display: flex;
                align-items: center;
                justify-content: center;
                @media(max-width:100em){
                    width: 4rem;
                    height: 4rem;
                }
                &:first-child{
                    margin-inline-end: 7px;
                }
            }
            ul{
                display: inline-flex;
                align-items: center;

            }

            .icon{
                width: 2rem;
                height: 2rem;
                fill: none !important;
                stroke: var(--color-white);
                @media(max-width:100em){
                    width: 1.8rem;
                    height: 1.8rem;
                }
            }
        }

        .location-details__info{
            margin-top: 2.7rem;
        }
        .location-details__info-group{
            span{
                &:first-child{
                    color: var(--color-black) ;
                }
            }
        }
        p{
            font-size: 1.5rem;
            color: var(--color-black);
            font-family: var(--font-regular);
            margin-top: 2rem;
            @media(max-width:90em){
                font-size: 1.4rem;
            }

            .read-more{
                color: #0094D1;
                font-family: var(--font-medium);
                background-color: transparent;
                border: none;
                cursor: pointer;
            }
        }
    }

    &__map{
        
        grid-area: map;
        height: 90vh;
        @media(max-width:62em){
            width: 50vw;
        }
        @media(max-width:47em){
            width: 100%;
            grid-column: 1/ -1;
            height: auto;
        }

        img{
            @extend%object-fit;
            object-fit: cover;
            object-position: top;
        }
    }

}
.readMore-content{
    display: none;
    &__warning{
        background-color: rgba(255, 45, 22, 0.205);
        padding: 3.3rem 2.5rem;
        display: flex;
        align-items: flex-start;
        margin-top: 3rem;
        @media(max-width:48em){
            padding: 15px;
        }
        &--green{
            background-color: rgba(10, 145, 46, 0.205);
            
        }

        &__content{
            h5{
                color: #00AEFC;
                font-size: 1.5rem;
                font-family: var(--font-medium);
                margin-bottom: 1.4rem;
            }
            p{
                color: #0A912F !important;
            }
        }

        p{
            margin-top: 0;
            color: #FF2D16;
            font-family: var(--font-regular);
        }

        &__icon{
            margin-inline-end: 2rem;
            .icon{
                height: 7rem;
                width: 3rem;
            }
        }
    }
}
.favourite-btn{
    .icon-fav{
        width: 1.5rem;
        height: 1.5rem;
        fill: none;
        stroke: var(--color-white);
    }
    &__input{
        display: none;
    }

    &__label{
        cursor: pointer;
    }

    &__input:checked ~ &__label .icon-fav{
        animation: favIconanim 1s ease-in-out;
        transform: scale(.8);
        animation-fill-mode: both;
    }
}

.image-scroll{
    margin-top: 4rem;
    overflow-x: auto;
    
    &__wrap{
        width: max-content;
    }

    ul{
        display: flex;

        li{
            width: 19rem;
            height: 22rem;
            border-radius: 2rem;
            margin-inline-end: 7px;
            overflow: hidden;
            position: relative;
            &::after{
                content: "";
                display: block;
                height: 100%;
                width: 100%;
                background-image: linear-gradient(to top, var(--color-black) 0 5% , rgba(0, 0, 0, 0) 50% 100%);
                position: absolute;
                top: 0;
                left: 0;
                z-index: 1;

            }
           

            img{
                @extend%object-fit;

            }
            span{
                position: absolute;
                left: 1.4rem;
                bottom: 1.4rem;
                font-size: 1.5rem;
                display: inline-block;
                font-family: var(--font-medium);
                color: var(--color-white);
                z-index: 2;
                text-transform: uppercase;
            }
        }
    }
}


.place-item{
    filter: saturate(0);
    cursor: pointer;
    &:hover{
        filter: saturate(1) !important;
    }
    &:first-child{
        margin-top: 2.5rem;
    }

    margin-top: 1.5rem;
    &__wrap{
        border-radius: 1.7rem;
        position: relative;
        overflow: hidden;
        height: 12.1rem;
        

        img{
           @extend%object-fit;
        }
    }

    &__content{
        position: absolute;
        top: 0;
        left: 0;
        padding: 1rem;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
    }

    &__head{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__title{
        display: flex;
        align-items: center;
        &__number{
            width: 1.8rem;
            height: 1.8rem;
            background-color: var(--color-orange);
            color: var(--color-white);
            font-size: 1.2rem;
            font-family: var(--font-medium);
            padding: 2px;
            margin-inline-end: 5px;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &__name{
            font-size: 1.6rem;
            font-family: var(--font-regular);
            @media(max-width:100em){
                font-size: 1.5rem;
            }
            @media(max-width:100em){
                font-size: 1.4rem;
            }

        }
    }

    &__fav{
       .icon-fav{
           width: 1.5rem;
           height: 1.5rem;
       } 
    }

    &__bottom{
        margin-top: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .distance-info{
            color: var(--color-white);
            .icon-distance{
                width: 6px;
                height: 12px;
                fill: var(--color-white);
            }

            &__number{
                font-size: 1.8rem !important;
                color: var(--color-white);
                
            }

            span{
                font-family: var(--font-light);
                font-size: 12px;
                color: var(--color-white);
            }
        }
    }
    &__icon-info-list{
        display: flex;
        align-items: center;
    }

    &__icon{
        width: 2.4rem;
        height: 2.4rem;
        border-radius: 50%;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        &:not(:last-child){
            margin-inline-end: 5px;
        }


        img{
            width: 1.6rem;
            height: auto;
        }
    }

   
}