// -----------------------------------------------------------------------------
// This file contains styles that are specific to the home page.
// -----------------------------------------------------------------------------



.section-image{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: 100vh;
    position: relative;
  }
  
  .slider-logo{
    max-height: 13.6rem ;
    max-width: 60rem;
    position: absolute;
    left: 4rem;
    top:50%;
    transform: translateY(-50%);
    text-align: left ;
    @media(max-width:100em){
      height: 11rem ;
      max-width: 45rem;
    }
    @media(max-width:90em){
      height: 9rem ;
      max-width: 40rem;
    }
    @media(max-width:37.5em){
      height: 7.4rem ;
      max-width: 32.5rem;
    }
  }
  
  .image-overColor{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.45);
    position: absolute;
    z-index: 0;
  }
  .video-control{
    position: absolute;
    z-index: 1;
    bottom: 4rem;
    right: 4rem;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    flex-direction: column;
  
    span{
      display: inline-block;
      color: var(--color-white);
      font-size: 1.6rem;
      cursor: pointer;
      margin-top: 6px;
    }
  }
  #section1{
    overflow: hidden;
    background-position: center;
    background-size: cover;
    position: relative;
    }
    .section-content{
    /*border:1px solid #F00;*/
      width:100%;
      margin:0 auto;
      text-align:center;
      overflow: hidden;
  
      h1{
        text-transform: uppercase;
        font-size: 10rem;
        font-family: var(--font-bold);
        color: var(--color-white);
        line-height: 1.1;
        @media(max-width:100em){
          font-size: 7rem;
         }
         @media(max-width:90em){
          font-size: 6rem;
         }
        @media(max-width:37.5em){
          font-size: 5rem;
        }
  
        span{
          display: block;
          font-size: 4rem;
          line-height: 1;
          @media(max-width:100em){
            font-size: 3rem;
           }
           @media(max-width:90em){
            font-size: 2.8rem;
           }
          @media(max-width:37.5em){
            font-size: 2.5rem;
          }
        }
      }
  
      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: center;
        overflow: hidden;
      }
    }
    h1{
      margin:0;
      color:#333;
    }
    h1 i.fa{
    font-size:3em;
    color:#FFF;
    transition:all 0.3s ease-in-out;
    }
    i.fa:hover{
    color:#f00;
    cursor:pointer;
    transform:scale(1.2) rotate(10deg);
    }  
    #section2{
    background-color:#0F0; 
    }
    #section3{
    background-color:#0ff; 
    }
    .slide{
    background-size:cover;
      background-position:center;
    }  
    #slide1{
    background-image:url(http://sites.psu.edu/siowfa15/wp-content/uploads/sites/29639/2015/10/cat.jpg);
    }  
    #section4{
    background-color:#999; 
    }
    #fp-nav ul li a span, 
    .fp-slidesNav ul li a span {
      width: 16px !important;
      height: 16px !important;
      margin: -9px 0 0 -4px;
      background: transparent;
      border: 1px solid #ffffff;
    }

    #fp-nav ul li a.active + .fp-tooltip{
        font-family: var(--font-medium) !important;
    }
    
    #fp-nav ul li a.active span, 
    .fp-slidesNav ul li a.active span, 
    #fp-nav ul li:hover a.active span, 
    .fp-slidesNav ul li:hover a.active span {
      width: 16px;
      height: 16px;
      margin: -9px 0 0 -4px;
      background: white;
      box-sizing: border-box;
      border: 1px solid #ffffff;
    }

   #fp-nav ul li:hover a span{
      margin: -9px 0 0 -4px;
    }
  
  
    
  
    .side-nav{
      width: 8rem;
      position: absolute;
      right: 4rem;
      top: 50%;
      transform: translateY(-50%);
      margin-top: 3.5rem;
      display: block;
     

      @media(max-width:37.5em){
       display: none;
      }
  
      &__small-img{
        width: 7rem;
        height: 4.2rem;
        border-radius: 5px;
      }
  
      span{
        font-family: var(--font-medium);
        color: var(--color-white);
        display: inline-block;
        font-size: 1.4rem;
      }

      ol>li:hover{
        
          img{
             &:hover{
              border: 1px solid var(--color-white);
            }
        }
      }
      
     ol{
       text-align: center;
       margin: 0 auto;
       list-style: none;
  
       li{
         list-style: none;
         cursor: pointer;
          
        
        &:not(:last-child){
          margin-bottom: 3.2rem;
          @media(max-width:100em){
            margin-bottom: 2.9rem;
           }
           @media(max-width:90em){
            margin-bottom: 2.7rem;
           }
           @media(max-height:768px){
             margin-bottom: 1.5rem;
           }
        }
       }
    
      }
      .map-btn{
        background-color: var(--color-drak-blue);
        width: 7rem;
        border-radius: 1.5rem;
        text-align: center;
        padding: 6px;
        vertical-align: middle;
      }
    }
  
  //   #section4{
  //     min-height: calc(100vh + 30rem) !important;
  // }
  
  #section1 #menu{
    display:none !important;
  }
  
  
  
  .home-page.fp-viewing-0 .side-nav{
    display:none !important;
  }

  .home-page.fp-viewing-0 #fp-nav{
    display:none !important;
  }

  .home-page{
    #fp-nav{
      top: 16rem !important;
      ul{
        li{
          &:first-child{
            display: none !important;
          }
        }
      }
    @media(min-width:37.5rem){
        display: none !important;
    }
    }
  }

  .home-page.fp-viewing-secondSection{
    #fp-nav{
      display: none !important;
    }
  }

  .home-page.fp-viewing-4{
    .side-nav{
      display: none !important;
    }
  }


 .fp-tooltip{
    opacity: 1 !important;
    width: auto !important;
    font-size: 12px !important;
    font-family: var(--font-light) !important;
  }

  #fp-nav ul li{
    width: 16px !important;
    height: 16px !important;
  }

  .scroll-arrow{
    position: absolute;
    left: 50%;
    bottom: 4rem;
    transform: translateX(-50%);
    cursor: pointer;
  }
  #fp-nav{
    z-index: 70;
  }