.slider-img{
    height:37.7rem;
    overflow: hidden;
    @media(max-width:100em){
      height: auto;
    }
    @media(max-width:90em){
      height: auto;
    }

    img{
       @extend%object-fit;
       height: 100vh !important;
    }
}

.slider{
    max-width: 75.5rem;
    @media(max-width:62em){
      max-width: 100%;
    }

    .slick-arrow{
        z-index: 10;
       bottom: 0;
       transform: translateY(135px);
       display: none !important;
       @media(max-width:100em){
        transform: translateY(107px);
       }
       @media(max-width:90em){
        transform: translateY(75px);
       }
    }

    .slick-dots {
        text-align: left;
        left:2.4rem;
        bottom: 4.1rem;
        li button:before,
        li.slick-active button:before {
          color: transparent;
          opacity: 1;
          margin: 5px;
        }
        li button:before{
          background-color: transparent;
          border: 1px solid #fff;
          border-radius: 50%;
          display: inline-block;
          height: 15px;
          width: 15px;
      
        }
        li.slick-active button:before {
          background-color: #fff;
        }

        li{
            margin-inline-end: 1.5rem;
        }
      }
      .slick-prev{
          left: 2.5rem;
          &::before{
            content: "";
            background-image: url(../img/arrow-white-slick.svg);
            background-repeat: no-repeat;
            background-size: contain;
            display: inline-block;
            width: 8px;
            height: 16px;
            transform: scale(-1);
        }
      }
      .slick-next{
          left:15rem;

          &::before{
              content: "";
              background-image: url(../img/arrow-white-slick.svg);
              background-repeat: no-repeat;
              background-size: contain;
              display: inline-block;
              width: 8px;
              height: 16px;
          }
      }
}

