.popup-slide-1, .popup-slide-2, .popup-slide-3, .popup-slide-4, .popup-slide-5 {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left:0;
    z-index: 1022;
    background-color: rgba(0, 0, 0, 0.705);
    visibility: hidden;
    transform: translateX(2000px);
    transition: all .2s;
    transition-timing-function: cubic-bezier(.28,.19,.73,.84);
}

.popup-slide-5{
    transition-delay: .4s;
   .menu{
       padding: 5rem;
   }
}

.popup-slide-2{
    transition-delay: .4s;
}

.map-slide{
    background-color: transparent;
    display: block;
    transform: translateX(2000px) ;
    visibility: hidden;
    position: absolute;
    z-index: 1;
    right: 0;


    .close-btnmap{
        transform: translate(3rem, 11rem);
        display: inline-block;
        cursor: pointer;
    }

    .map-img{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
}

.menu{
    width: 49.3rem;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--color-drak-blue-1);
    display: block;
    @media(max-width:100em){
        width: 44rem;
    }
    @media(max-width:90em){
        width: 40rem;
    }

    &__list{
        padding: 5rem 4rem;
        @media(max-width:90em){
            padding: 3rem 3rem;
        }
    }

    &__list-normal{
        li{
            color: var(--color-white);
            font-size: 2rem;
            margin-top: 2rem;
            display: block;
            cursor: pointer;
            &:first-child{
                margin-top: 1rem;
            }

            @media(max-width:90em){
                font-size: 1.8rem;
            }
        }
        li > a{
            color: var(--color-white);
            font-size: 2rem;
            display: block;
            @media(max-width:90em){
                font-size: 1.8rem;
            }
        }
    }

    &__header{
        min-height: 24.6rem;
        display: flex;
        @media(max-width:90em){
            min-height: 18rem;
        }
        &__start{
            width: 38.6rem;
            background-color: var(--color-orange);
            color: var(--color-white);
            align-self: stretch;
            border-radius: 0 0 12.5rem 0;
            padding: 7rem 4rem;
            display: flex;
            align-items: center;
            @media(max-width:90em){
                padding: 6rem 2rem;
            }
        
           
            .user-name{
                font-size: 2.2rem;
                font-family: var(--font-regular);
                display: block;
                @media(max-width:90em){
                    font-size: 2rem;
                }
            }
            .log-btn{
                cursor: pointer;
                display: inline-block;
                @media(max-width:90em){
                    font-size: 1.3rem;
                }
                
            }
        }

        &__end{
            display: flex;
            justify-content:space-between;
            flex-direction: column;
            align-items: center;
        }
    }
}

.user-image{
    width: 9rem;
    height: 9rem;
    border-radius:50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-inline-end: 2.2rem;
    border: 2px solid var(--color-light-blue);
    @media(max-width:100em){
        width: 8rem;
        height: 8rem;
    }
    @media(max-width:90em){
        width: 7rem;
        height: 7rem;
    }

    .icon-guest{
        width: 4.8rem;
        height: 4.8rem;
        @media(max-width:100em){
            width: 4rem;
            height: 4rem;
        }
        @media(max-width:90em){
            width: 3rem;
            height: 3rem;
        }
        
    }
    .user-img{
        @extend%object-fit;
        object-position: top center;
        display: none;
    }
}

#login{
    display: none;
}

.login{
    width: 40rem;
    padding: 4.4rem 3rem;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 3rem;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: var(--color-white);
    text-align: center;
    @media(max-width:90em){
        padding: 4rem 2.6rem;
    }
    

    input{
        height: 5rem;
        width: 100%;
        border: none;
        border-radius: 3rem;
        background-color: rgba(255, 255, 255, 0.301);
        padding: 1.6rem 2rem;
        outline: none;
        color: var(--color-white);
        font-family: inherit;
        @media(max-width:90em){
            height: 4rem;
        }
        &::placeholder{
            font-family: var(--font-light);
            font-size: 1.6rem;
            opacity: .4;
            color: var(--color-white);
        }
        &:first-child{
            margin-bottom: 1.4rem;
        }
    }

    .forget-psword{
        color: rgba(255, 255, 255, 0.80);
        display: block;
        text-align: right;
        margin: 10px 0;
        font-size: 1.4rem;
    }

    .login-btn{
        height: 5.4rem;
        width: 100%;
        border: none;
        background-color: var(--color-orange);
        font-family: var(--font-medium);
        display: block;
        border-radius: 3rem;
        margin: 2rem 0;
        font-size: 1.6rem;
        letter-spacing: 3px;
        color: var(--color-white);
        cursor: pointer;
        @media(max-width:90em){
            height: 4rem;
        }
    }

    .btn-guest{
        display: block;
        font-size: 1.8rem;
        font-family: var(--font-medium);
        color: var(--color-white);
    }

    .social-login{
        margin: 3rem 0;
        span{
            display: block;
            margin-bottom: 1.7rem;
        }

        .icon{
            display: inline-block;
            width: 4rem;
            height: 4rem;
            margin: 0 6px;
            fill: var(--color-white)
        }
    }

    .sign-up-text{
        opacity: .4;
        font-size: 1.6rem;
       
    }
    .sign-up-btn{
        font-family: font(--font-medium);
        font-size: 1.6rem;
        display: inline-block;
        margin-inline-start: 6px;
        cursor: pointer;
    }
    .download-app{
        position: relative;
       margin-top: 4.4rem;
        span{
            display: block;
            margin-bottom: 1rem;
            letter-spacing: 1.5px;
        }

        &::before{
            content: "";
            display: inline-block;
            top: -2rem;
            left: -3rem;
            position: absolute;
            z-index: 1;
            width: calc(100% + 6rem);
            height: 1px;
            background-color: #707070;
            @media(max-width:90em){
                width: calc(100% + 5rem);
                left: -2.58rem;
            }
        }
    }
}


#createAccount{
    display: none;
}
.create-account{
    input{
        &::placeholder{
            color: var(--color-white);
            opacity: 0.4;
        }
        &:not(:last-child){
            margin-bottom: 10px;
        }
}

.back-btn{
        color: rgba(255, 255, 255, 0.5);
        display: flex;
        align-items: center;
        text-align: center;
       justify-content: center;
        .icon{
            width: 7.5px;
            height: 15px;
            stroke: rgba(255, 255, 255, 0.5);
           margin-inline-end: 5px;
        }
    }
}

.popup-slide-2, .popup-slide-3{
    .menu{
        padding: 5rem 4.4rem;
        @media(max-width:47em){
            padding: 15px;
        }
    }
}
.popup-slide-3{
    .close-btn{
        padding-inline-end: 5rem !important;
        @media(max-width: 47em){
            padding-inline-end: 15px !important;
        }
    }
    .menu{
        padding-inline-end:0;
        @media(max-width:100em){
            width: 47rem;
        }
    }
}
.setting-popup{
    overflow: hidden;
    :-webkit-scrollbar{
        display: none;
    }
    &:hover{
        :-webkit-scrollbar{
            display: block;
        }
    }

    &__wrap{
        height:85vh ;
        overflow-y: auto;
        padding-inline-end: 4.4rem;
        @media(max-width:47em){
            padding-inline-end: 15px;
            height: 89vh;
        }
        
    }

    .filter-result{
        display: flex;
        text-align: center;
        font-size: 2rem;
        color: var(--color-light-blue);
        justify-content: center;
        margin-top: 5rem;
        @media(max-width:100em){
            margin-top: 2.8rem;
        }
    }
    
    &__head{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__start{
        span{
            display: inline-block;
            color: var(--color-orange);
            text-transform: capitalize;
            font-size: 2.4rem;
            font-family: var(--font-regular);
            @media(max-width:100em){
                font-size: 2rem;
            }
        }
    }

    &__end{
        .close-btn{
            padding: 0 ;
            
           &::after {
                content: "";
                background-image: url(../img/close-btn.svg);
                width: 1.7rem;
                height: 1.7rem;
                background-size: contain;
                background-position: center;
                display: inline-block;
            }
        }
    }

    .btn-settings{
        display: inline-block;
        background-color: var(--color-orange);
        color: var(--color-white);
        font-size: 13px;
        font-family: var(--font-bold);
        letter-spacing: 3px;
        text-transform: uppercase;
        width: 100%;
        border-radius: 2.1rem;
        padding: 1.2rem 2rem;
        border: none;
        margin-top: 5rem;
        height: 4.2rem;
        cursor: pointer;
        @media(max-width:100em){
            margin-top: 3rem;
        }
    }
}

.setting-group{
    .fake-item{
        min-width: 8rem;
        border: none;
    }
    margin-top: 5rem;
    @media(max-width:100em){
        margin-top: 4rem;
    }
    @media(max-width:90em){
        margin-top: 3rem;
    }
    color: var(--color-white);
   


    &__item{
        
    }

    &__wrap{
       
    }

    &__act-wrap{
        display: flex;
        justify-content: space-between;
    }

    &__radio-item{
        display: inline-flex;
        &:not(:last-child){
            margin-inline-end: 1.2rem;
        }
    }

    &__title{
        font-size: 1.6rem;
        font-family: var(--font-regular);
        display: flex;
        justify-content: space-between;
        margin-bottom: 12px;
        text-transform: uppercase;
    }

    &__radio-custom{
        background-color: transparent;
        color: var(--color-light-blue);
        padding: 0 1.4rem;
        min-width: 8rem;
        height: 3.5rem;
        border-radius: 2rem;
        text-transform: uppercase;
        font-size: 12px;
        border: 1px solid var(--color-light-blue);
        cursor: pointer;
        display: inline-block;
        text-align: center;
        font-family: var(--font-book);
        width: max-content;
        height: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;

        &__subtitle{
            font-size: 12px;
            color: var(--color-light-blue);
            display: inline-block;
            margin-top: 5px;
        }

        &--round{
            width: 4.2rem;
            height: 4.2rem;
            border-radius: 50%;
            min-width: 4.2rem;
            padding: 0;
            margin: 0 auto;

            svg{
                width: 2.6rem;
                height: 2rem;
                fill: var(--color-light-blue) !important;
            }
        }
       
    }

    &__radio-wrap{
        display: inline-flex;
        align-items: center;
    }

    &__input{
        display: none;
    }

    &__input:checked + &__label &__radio-custom{
        background-color: var(--color-light-blue);
        color: var(--color-white);
        svg{
            fill: var(--color-white) !important;
        }
       
    }

    &__label{
        display: inline-block;
    }

}

.radio-scroll{
    .slick-slide{
        margin: 0 1rem;
    }
.setting-group__radio-item{
    margin: 0;
}

}

.profile-editpopup{
    width: 42rem;
    height: 100vh;
    background-color: var(--color-drak-blue-1);
    padding: 5rem;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(500px);
    z-index: 999;

    &__head{
        display: flex;
        text-align: center;
        justify-content: center;
        align-items: center;
        margin-bottom: 5rem;

        &__title{
            font-size: 2.4rem;
            font-family: var(--font-regular);
            color: var(--color-orange);
            margin-inline-start: auto;
        }

        .closeBTN{
            margin-inline-start: auto;
            cursor: pointer;
        }
    }
}

.user-profile{

    &__image-wrap{
        position: relative;
        display: inline-block;
        left: 50%;
        transform: translateX(-50%);
        margin-bottom: 3.6rem;
    }
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position:center;
        width: 10.2rem;
        height: 10.2rem;
        border-radius: 50%;
        overflow: hidden;
        border: 1px solid var(--color-light-blue);
        display: inline-block;
    }

    &__picedit{
        position: absolute;
        z-index: 1;
        width: 2.7rem;
        height: 2.7rem;
        border-radius: 50%;
        background-color: #91B9B4;
        right: 0;
        bottom: 1rem;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;

        input{
            display: none;
        }

        label{
            cursor: pointer;
           
            .icon{
                width: 1.3rem;
                height: 1.3rem;
                stroke: var(--color-white);
                display: inline-block;
                margin-top:5px;
                
            }
        }
    }

    &__group{
        margin-bottom: 1.5rem;
        position: relative;
    }

    &__input{
        width: 100%;
        height: 4.2rem;
        border-radius: 2.5rem;
        background-color: rgba(255, 255, 255, 0.205);
        border: none;
        color: var(--color-white);
        font-family: var(--font-light);
        font-size: 1.4rem;
        padding: 1.2rem 2.5rem;

        &:focus{
            outline: none;
        }

        &::placeholder{
            color: rgba(255, 255, 255, 0.699);
        }

    }

    &__input-mail
        :focus:invalid .email-check{

        display: none;
    }

    &__changepas{
        display: block;
        font-family: var(--font-light);
        color: var(--color-white);
        opacity: .8;
        margin-top: 3rem;
        margin-bottom: 1.2rem;
    }

    .btn-update{
        width: 100%;
        height: 4.2rem;
        border-radius: 2.5rem;
        border: none;
        background-color: var(--color-orange);
        color: var(--color-white);
        text-transform: uppercase;
        font-family: var(--font-bold);
        letter-spacing: 3px;
        cursor: pointer;
        margin-top: 3rem;

    }

    &__del{
        display:inline-block;
        margin-top: 5rem;
        font-size: 1.5rem;
        color: var(--color-white);
        cursor: pointer;
    }

    .email-check{
        width: 1.4rem;
        height: 1.4rem;
        position: absolute;
        right: 2.6rem;
        top:50%;
        transform: translateY(-50%);
    }
    
}

.feedback{
    margin-top: 5rem;
    @media(max-width:100em){
       margin-top: 3rem;
    }
    .btn-settings{
        margin-top: 3rem !important;
    }

    &__text-area{
        width: 100%;
        background-color: rgba(255, 255, 255, 0.2);
        border-radius: 2.5rem;
        border: none;
        padding: 1.2rem 2rem;
        color: var(--color-white);
        font-size: 1.4rem;
        font-family: var(--font-light);

        &:focus{
            border: none;
            outline: none;
        }

        &::placeholder{
            color: rgba(255, 255, 255, 0.411);
        }
    }
    &__group{
        margin-bottom: 1.5rem;
        select{
            width: 100%;
            height: 4.2rem;
            background-color: rgba(255, 255, 255, 0.205);
            border: none;
            outline: none;
            border-radius: 2.5rem;
            padding: 1.2rem 2.5rem;
            &:focus{
                outline: none;
            }
        }

        .select2-selection{
            width: 100%;
            height: 4.2rem;
            font-size: 1.4rem;
            background-color: rgba(255, 255, 255, 0.2);
            border: none;
            padding: 5.5px 2rem;
            border-radius: 2.5rem;
            color: var(--color-white);
        }

        
            width: 100%;
           
    
    
            .file-drop-area {
                display: flex;
                position: relative;
                align-items: center;
                justify-content: center;
                width: 100%;
                max-width: 100%;
                padding: 3.3rem;
            //    border: dashed 1px #dddddd7e;
                border-radius: 2.5rem;
                transition: 0.2s;
                height: 13rem;
                background-color: rgba(255, 255, 255, 0.199);
                background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='24' ry='24' stroke='lightblue' stroke-width='2' stroke-dasharray='15' stroke-dashoffset='4' stroke-linecap='square'/%3e%3c/svg%3e");
                border-radius: 24px;
border-radius: 23px;

    
                @media (max-width: 767px) { 
                    flex-direction: column;
                }
                &.is-active {
                  background-color: rgba(255, 255, 255, 0.05);
                }
              }
              
              .fake-btn {
                text-transform: capitalize;
                text-decoration: underline;
                color: rgba(255, 255, 255, 0.507);
                padding-left: .5rem;
                font-family: var(--font-light);
                font-size: 1.4rem;
              }
              
              .file-msg {
                font-size: 1.8rem;
                font-weight: 300;
                line-height: 1.4;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;

                svg{
                    width: 3rem;
                    height: 3rem;
                    stroke: rgba(255, 255, 255, 0.459);
                }
              }
              
              .file-input {
                position: absolute;
                left: 0;
                top: 0;
                height: 100%;
                width: 100%;
                cursor: pointer;
                opacity: 0;
                &:focus {
                  outline: none;
                }
              }
        }

}

.select2-container--default .select2-selection--single .select2-selection__rendered{
    color: var(--color-white);
}

.select2-container--default .select2-selection--single .select2-selection__arrow b{
    background-image:url(../img/arrow-drop.svg) !important;
    background-color: transparent;
    background-size: contain;
    border: none !important;
    height: 20px !important;
    width: 20px !important;
    margin: auto !important;
    background-repeat: no-repeat;
    left: -100% !important;
    top: 55% !important;
}

