// -----------------------------------------------------------------------------
// This file contains all styles related to the button component.
// -----------------------------------------------------------------------------

.btn:link,
.btn:visited{
    outline: none;
    text-decoration: none;
}

.btn{
   width: 14rem;
    height: 3.3rem;
    border: none;
    text-align: center;
    margin-top: 2rem;
    transition: all .2s ease-in;
    border-radius: 2.5rem;
    padding: 4px;
    &--primary{
        background-color: var(--color-orange);
        color: var(--color-white);
        font-size: 1.7rem;
        font-family: var(--font-medium);
    }


}





.btn:hover{
    background-color: var(--color-black);
    color: var(--color-white);
   
}

.btn:focus{
    box-shadow: none;
}




.btn-feedback{
    width: 14rem;
    border-radius: 2.5rem;
    background-color: var(--color-white);
    border: 1px solid var(--color-drak-blue);
    padding: 1rem 3rem;
    color: var(--color-drak-blue);
    margin-top: 2rem;
    display: block;
    font-family: var(--font-regular);
    font-size: 1.3rem;
    text-align: center;
    display: none;
}

.search-icon{
position: relative;
}
.search-icon,
.icon-filter{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: var(--color-white);
    margin-inline-end: 3rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    @media(max-width:100em){
        width: 3.5rem;
        height: 3.5rem;
    }
    @media(max-width:37.5em){
        margin-inline-end: 2rem;
        width: 3.5rem;
        height: 3.5rem;
      }

    .icon-search{
        width: 2.2rem;
        height: 2.2rem;
        @media(max-width:100em){
            width: 2.1rem;
            height: 2.1rem;
        }
    }
}

.icon-filter{
    img{
        @media(max-width:37.5em){
           transform: scale(.8);
          }
    }
        }

.lang-btn{
    padding: 1rem 2rem;
    min-width: 8.6rem;
    height: auto;
    background-color: #B41932;
    color: var(--color-white);
    border: none;
    border-radius: 2.1rem;
    justify-self: flex-end;
    cursor: pointer;
    font-size: 2rem;
    font-family: var(--font-medium);
     align-self: flex-start;
     margin-left: -1rem;
}

.close-btn{
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 3rem 4rem;
    &::after{
        content: "";
        background-image: url(../img/close-btn.svg);
        width: 3rem;
        height: 3rem;
        background-size: contain;
        background-position: center;
        display: inline-block;
        @media(max-width:90em){
            width: 2.6rem;
            height: 2.6rem;
        }
    }
}

.btn-clear{
    cursor: pointer;
    text-transform: capitalize;
    color: #91B9B4;
    font-size: 1.4rem;
}

.d-noneBtn{
    position: absolute;
    top: 2rem;
    left: 2rem;
    z-index: 1;
    text-transform: uppercase;
    color: var(--color-orange);
     font-size: 1.8rem;
    
    @media(min-width:62em){
        display: none;
        
    }

    svg{
        width: 0.7rem;
    height: 1.4rem;
    stroke: var(--color-orange);
    -webkit-margin-end: 0.7rem;
    margin-inline-end: 0.7rem;
    }
}

.shareBtn, .shareBtnScr{
    cursor: pointer;
}

.buttonCombo-warp{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 7.1rem;
    min-width: 25.9rem;
}

.explore-btn{
    background-color: var(--color-orange);
    color: var(--color-white);
    padding: 1.2rem 2rem;
    min-width: 25.9rem;
    border-radius: 2.5rem;
    text-align: center;
    cursor: pointer;
    text-transform: uppercase;
    font-family: var(--font-bold);
    font-size: 1.6rem;
    letter-spacing: 4px;
    display: inline-block;
  }

.mobile-mapBtn{
    height: 4.6rem;
    border-radius: 2.5rem;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    padding: 4px;
    margin-left: -4rem;
    background-color: var(--color-drak-blue);
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 6rem;
    [dir='rtl'] &{
        border-bottom-left-radius: 0;
        border-top-left-radius: 0;
    }
    svg{
        stroke: var(--color-white);
        width: 20px;
        height: 20px;
        margin-inline-start: 5px;
    }

    @media(min-width:47em){
        display: none;
    }
}