
.map-nav > svg{
  height: 50px;
  @media(max-width:100em){
    height: 45px;
   }
  @media(max-width:37.5em){
   display: none;
  }
}
.map-nav{

  &--mobile{
    @media(min-width: 37.5rem){
      display: none;
    }
  }
    
    z-index: 99;
    top: 9rem;
    left: 50%;
    transform: translateX(-50%);
    position: fixed;
    @media(max-width: 100em){
      top: 8rem;
    }
    @media(max-width: 90em){
      top: 7rem;
    }
    @media(max-width:37.5em){
      background-color: var(--color-black);
      width: 100%;
      height: 6rem;
      display: flex;
      align-items: center;
      top: unset;
      bottom: 0;

    }
  
    &__list{
      position: absolute;
      top: 0;
      left: 50%;
      width: 100%;
      text-align: center;
      transform: translateX(-50%);
      @media(max-width:37.5em){
       background-color: var(--color-black);
       position: relative;
       display: flex;
       justify-content: space-between;
       }
      .icon-mapNav{
        stroke: var(--color-white);
        width: 2rem;
        height: 2rem;
      }
     
    }
  
    &__item{
      text-align: center;
      display: inline-block;
      cursor: pointer;

      &:hover{
        svg{
          stroke: var(--color-orange) !important;
        }
        span{
          color:  var(--color-orange) !important;
        }
      }
     
        margin: 0 3.1rem;
      
      span{
        display: block;
        color: var(--color-white);
        font-size: 1.1rem;
        font-family: var(--font-book);
      }
    }
  }

  .vertical-scrolling{
    position: relative;
  }
  
  .location-details{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 4rem;
    height: 100vh;
    align-items: flex-end;
    padding-bottom: 5rem;
    z-index: 2;
    position: relative;
    @media(max-width:37.5em){
      align-items: flex-start;
      margin: 0 1.5rem;
      margin-top: 9rem;
    }
  
    &__start{
      @media(max-width:37.5em){
      display: flex;
      flex-direction: column;
      height: 100%;
      }
      .btn{
        display: block;
        margin-top: 3rem;
        @media(max-width:37.5em){
          margin-bottom: 12.5rem;
        }
      }
    }
  
    &__end{
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      .favourite-btn{
        .icon-fav{
          width: 3rem;
          height: 3rem;
        }
        margin-bottom: 2rem;
      }
      @media(max-width:37.5em){
        align-self: flex-end;
        margin-bottom: 12rem;
      }
      
      .icon-share {
        width: 3rem;
        height: 3rem;
        margin-bottom: 2.8rem;
        stroke: var(--color-white);
        fill: transparent;
        @media(max-width:37.5em){
          margin-bottom: 2rem;
        }
      }
  
      .icon-distance{
        width: 1.2rem;
        height: 1.2rem;
        position: absolute;
        top: 1.5rem;
        left: -1.5rem;
        fill: var(--color-white);
  
      }
  
      .distance-info{
        position: relative;
        &__number{
          font-size: 3.4rem !important;
        }
        color: var(--color-white);
        span{
          display: inline-block;
          font-size: 2.1rem;
        }
      }
    }
    &__info{
      margin-top: 2rem;
      display: block;
    }
  
    &__info-group{
      span{
        display: inline-block;
        font-size: 1.5rem;
        &:first-child{
          color: var(--color-white);
          margin-inline-end: 1rem;
          
        }
        &:last-child{
          color: var(--color-light-blue);
          font-family: var(--font-medium);
          text-transform: uppercase;
        }
      }
    }
    &__header{
      display: flex;
      flex-wrap: wrap;
    }
  
    &__number{
        min-width: 5rem;
        min-height: 5rem;
        background-color: var(--color-orange);
        color: var(--color-white);
        font-family: var(--font-medium);
        font-size: 2.8rem;
        display: flex;
        justify-content: center;
        align-items: center;
        align-self: center;
        @media(max-width:37.5em){
          min-width: 2.6rem;
          min-height: 2.6rem;
          font-size: 1.8rem;
        }
    }
  
    &__title{
      margin-inline-start: 1.8rem;
      
      &__main{
        font-size: 2.8rem;
        font-family: var(--font-medium);
        color: var(--color-white);
        display: block;
        @media(max-width:37.5em){
          font-size: 2rem;
        }
      }
  
      &__sub{
        font-size: 2rem;
        font-family: var(--font-regular);
        color: #E8E8E8;
        @media(max-width:37.5em){
          display: none;
        }
      }
    }
  
    &__icon-info-list{
     margin-top: 2rem;
     display: inline-flex;
     align-items: center;
     @media(max-width:37.5em){
          margin-bottom: auto;
    }
  
    }
  
    &__icon{
      width: 5rem;
      height: 5rem;
      display: inline-block;
      border-radius: 50%;
      font-size: 1.3rem;
      color: var(--color-white);
      display: inline-flex;
      justify-content: center;
      align-items: center;
      @media(max-width:100em){
        width: 4rem;
        height: 4rem;
      }
      &:not(:last-child){
        margin-inline-end: 1.5rem;
      }

      img{
        @media(max-width:100em){
          transform: scale(.8);
        }
        @media(max-width:37.5em){
          transform: scale(.7);
        }
      }
      @media(max-width:37.5em){
      width: 3.5rem;
      height: 3.5rem;

      
      }
      &--black{
        background-color: var(--color-black);
        font-family: var(--font-medium);
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        span{
          display: block;
          &:last-child{
            font-size: 1rem;
          }
        }
      }
  
      &--camp{
        background-color: #285A75;
      }
      &--climp{
        background-color: #EF8D50;
      }
      &--hiking{
        background-color: #C6CA00;
      }
    }
  }

  .detail-page #fp-nav {
    top: 50% !important;
    @media(max-width:65em){
      top: 15rem;
    }
}

.detail-page.fp-viewing-fourthSection{
  #fp-nav{
    display: none !important;
  }
}

.detail-page.fp-viewing-fourthSection{
  .map-nav{
    display: none !important;
  }
}


#fullpage-detail{
  .location-details__info{
    display: none;
  }
}