.notification{
    position: fixed;
    bottom: 2.5rem;
    right: 2.5rem;
    padding: 3.5rem;
    background-color: var(--color-black);
    color: var(--color-white);
    font-family: var(--font-light);
    border-radius: 2.5rem;
.icon-close{
    width: 3rem;
    height: 3rem;
}

}

.notification-startPoint{
    display: none;
}

.notification-photo{
    display: none;
}

.notification-instruction{
    display: none;

}
.notification-legend{
    display: none;
    .notification-start__head__start{
        span{
            font-family: var(--font-regular);
            font-size: 1.6rem;
        }
    }
}

.notification-download{
    display: none;
    .close-notf{
        transform: scale(.8);
    }

    .notification-start__head__start{
        display: flex;
        flex-direction: column;
        span{
            display: block;
            font-size: 1.6rem;
            &:first-child{
                font-family: var(--font-regular);
            }
            &:nth-child(2){
                font-family: var(--font-light);
                margin-bottom: 1.5rem;
            }
        }
    }
}



.the-window{
    display: none;

    .notification-start__info-num{
        border-radius: 0;
        background-color: #0094D1;
        border: 1px solid #FAE708;

        
    }
    .notification-start__info-num + span{
        img{
            transform: translateY(-2px);
        }
    }
}


.notification-start{
    width: 70rem;

    &--small{
        width: 25.4rem;
    }

    &__para{
        font-family: var(--font-light);
        font-size: 1.6rem;
        margin-bottom: 2.5rem;
    }

    &__head{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        .close-notf{
            cursor: pointer;

            .icon-close{
                width: 2rem;
            height: 2rem;
            }
        }

        &__start{
            display: flex;
            align-items: flex-start;
            font-size: var(--font-medium);
            font-size: 1.8rem;
        }
    }

    &__middle{
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-top: 1rem;

        &__start{
            span{
                display: block;
            }
        }

        &__end{
            .btn-group{
                display: inline-flex;
                
                span{
                    min-width: 8rem;
                    background-color: #91B9B4;
                    color: var(--color-white);
                    display: inline-block;
                    height: 3rem;
                    border-radius: 2.5rem;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    font-size: 12px;
                    &:last-child{
                        margin-inline-start: 2.2rem;
                    }
    
                    svg{
                        width: 1.7rem;
                        height: 1.7rem;
                        margin-inline-start: 10px;
                    }
                }
            }
        }
    }

    &__middle-layout{
        display: flex;
        margin-top: 2rem;
        margin-bottom: 3.2rem;
        &__start{
            flex: 0 1 27.3rem;
            height: 31.7rem;

            img{
                @extend%object-fit;
                object-position: center;
            }
        }

        &__end{
            flex: 1;
            margin-inline-start: 2.5rem;
            p{
                font-size: 12px;
                font-family: var(--font-light);
                line-height: 1.6;
                &:first-child{
                    margin-bottom: 2.5rem;
                }
            }
        }
    }

    &__info-num{
        background-color: #EB5A3C;
        color: var(--color-white);
        font-size: 1.4rem;
        font-family: var(--font-bold);
        padding: 3px;
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: 1;
        flex-direction: column;
        position: relative;

        img{
            position: absolute;
            bottom: -18px;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    &__info-num-title{
        margin-inline-start: 1rem;
        font-size: 1.8rem;
        font-family: var(--font-medium);
        color: #0094D1;
        
    }

   
    &__head-wrap{
        display: inline-flex;
        align-items: center;
        margin-bottom: 1.8rem;
        span{
            font-size: 1.8rem;
            font-family: var(--font-medium);
            color: #91B9B4;
        }
    }
    
    &__bg{
        width: 3.2rem;
        height: 3.2rem;
        border-radius: 50%;
        background-color: #479B6E;
        display: inline-flex;
        margin-inline-end: 1.5rem;

        svg{
            width: 3.2rem ;
            height: 3.2rem ;
        
    }
}
    &__start{
       display: flex;
       flex-direction: column;
       &__title{
           padding: 3px 1.8rem;
           background-color: #479B6E;
           align-self: flex-start;
           border-radius: 3px;
           position: relative;
           margin-bottom: 10px;
           font-size: 1.8rem;
           font-family: var(--font-medium);


           &::after{
               content: "";
               display: inline-block;
               width: 10px;
               height: 10px;
               background-color: #479B6E;
               position: absolute;
               bottom: -5px;
               left: 50%;
               transform: translateX(-50%) rotate(45deg);
               z-index: 1;
           }

       }

       &__title-loc{
           font-size: 1.6rem;
           font-family: var(--font-regular);

       }

       &__location{
           font-size: 1.6rem;
           font-family: var(--font-light);
       }
    }

    &__end{
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;

       
        
    }

}

.legend-group{
    display: flex;
    align-items: center;
    margin-bottom: 2.5rem;
    &:first-child{
        margin-top: 1rem;
    }
    &__icon{
        min-width: 9rem;
        display: flex;
        justify-content: center;
       
        .icon-loc{
        
        }
    }

    &__title{
        min-width: 20rem;
        margin-inline-start: 2rem;
    }

    .f-icon-bg{
        position: relative;
        &--1{
            background-color: #CC5557;
        }
        &--2{
            background-color: #1C455D;
        }
        &--3{
            background-color: #F3783A;
        }
        &--4{
            background-color: #A57AD6;
        }
        &--5{
            background-color: #CEC700;
        }
        &--6{
            background-color: #49C1C6;
        }
    }
}
.pop-up-notification-detail0{
    display: none;
}
.pop-up-notification-detail1{
    display: block;
    .pop-up-notification__content{
        width: 76.2rem;
        max-width: 76.2rem;
    }
   
}
.pop-up-notification{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    visibility: hidden;
    background-color: rgba(0, 0, 0, 0.596);
    z-index: 999;

    &__content{
        border-radius: 2.5rem;
        background-color: var(--color-black);
        color: var(--color-white);
        padding: 3.3rem;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 70rem;
        max-width: 70rem;
        @media(max-width:47em){
            width: 98vw;
            max-width: 98vw;
        }
    }
    &__content-head{
display: flex;
justify-content: space-between;
align-items: center;
margin-bottom: 2.7rem;

.icon-close{
    width: 18px;
    height: 18px;
    cursor: pointer;
}
.close-notf{
    height: 18px;
}
    }
    &__content-middle{
        display: flex;
        @media(max-width:47em){
           flex-direction: column;
        }
    }

    &__content-middle-start{
       width: 21.6rem;
       height: 10.6rem;
       flex-shrink: 0;
       @media(max-width:47em){
        width: 100%;
        height: auto;
        margin-bottom: 2rem;

     }
       img{
           @extend%object-fit;
       }
    }
    &__content-middle-end{
         margin-inline-start: 2.5rem;
         @media(max-width:47em){
            margin-inline-start: 0;
            margin-top: 2rem;
         }
         p{
             font-family: var(--font-light);
         }

         .title{
             font-family: var(--font-bold);
             font-size: 2.3rem;
             color: var(--color-white);
             display: block;
             
         }

         .title-sub{
             font-family: var(--font-regular);
             line-height: 1.8;
             display: block;

         }

         .icon-set{
             display: inline-flex;
             margin-top: 1.5rem;

             .f-icon-bg{
                position: static;
                &:not(:last-child){
                    margin-inline-end: 1rem;
                }

                &--1{
                    background-color: #CC5557;
                }
                &--2{
                    background-color: #1C455D;
                }
                &--3{
                    background-color: #F3783A;
                }
                &--4{
                    background-color: #A57AD6;
                }
                &--5{
                    background-color: #CEC700;
                }
                &--6{
                    background-color: #49C1C6;
                }
            }

         }
     }

    

    &__content-footer{
        margin-top: 3rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media(max-width:47em){
            flex-direction: column;
            align-items: flex-start;
        }
         

        .copy-link{
            font-family: var(--font-light);
            font-size: 1.4rem;
            @media(max-width:47em){
               margin-bottom: 5px;

            }
        }
        .btn-group{
            display: inline-flex;
            align-items: center;

            a{
                
                    margin-inline-start: 8px;
                
            }
    
            .icon{
                height: 2.2rem;
                width: 2.2rem;
                fill: var(--color-light-blue);
                
               
            }

            .icon-social{
                width: 3.1rem;
                height: 3.1rem;
            }
            
            span{
                min-width: 8rem;
                background-color: #91B9B4;
                color: var(--color-white);
                display: inline-block;
                height: 3rem;
                border-radius: 2.5rem;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                font-size: 12px;
                &:last-child{
                    margin-inline-start: 2.2rem;
                }

                svg{
                    width: 1.7rem;
                    height: 1.7rem;
                    margin-inline-start: 10px;
                }
            }
        }
    }

    &__content-middle-start{

    }
}