.activities-grid{
    display: grid;
    grid-template-columns: 39.2rem repeat(2, 1fr);
    height: 100vh;
    overflow: hidden;
    @media(max-width:100em){
        grid-template-columns: 35rem 1fr 1fr;
    }
    @media(max-width:90em){
        grid-template-columns: 35rem 1fr 1fr;
    }
    @media(max-width:62em){
        grid-template-columns: 33rem 1fr 1fr;
        grid-template-rows: 50vh;
    }
    @media(max-width:48em){
        grid-template-columns: auto;
        grid-template-rows: auto;
        height: auto;
    }
    

    &__side{
        background-color: var(--color-drak-blue);
        color: var(--color-white);
        height: calc(100vh - 9rem);
        overflow-y: auto;
        margin-top: 9rem;
        text-align: start;
        padding: 4rem;
        @media(max-width:100em){
            margin-top: 8rem;
        }
        @media(max-width:90em){
            margin-top: 7rem;
            height:100%;
        }
        @media(max-width:62em){
            height:100%;
            grid-row: span 2;
        }
        @media(max-width:48em){
            height:100%;
            grid-row: 1/2;
            grid-column: 1/ -1;
            margin-top: 6rem;
            padding: 15px;

        }

       

        .back-link{
            display: inline-flex;
            align-items: center;
            
        }

        span{
            color: var(--color-orange);
            font-size: 1.6rem;
            text-transform: uppercase;
        }
        .icon-back{
            width: .7rem;
            height: 1.4rem;
            stroke: var(--color-orange);
            margin-inline-end: .7rem;
        }
    }

    &__info{
        height: calc(100vh - 9rem);
        padding: 4rem;
        overflow-y: auto;
        margin-top: 9rem;
        @media(max-width:100em){
            margin-top: 8rem;
        }
        @media(max-width:90em){
            margin-top: 7rem;
        }
        @media(max-width:62em){
            grid-column: 2/4;
            grid-row: 2/3;
            height: auto;
            margin-top: 0;
        }
        @media(max-width:48em){
            grid-column:1/-1;
            grid-row: 2/3;
            padding: 15px;
            margin-top: 6rem;
        }
    }

    &__image-slider{
        overflow: hidden;
        height: 100%;
        @media(max-width:62em){
            grid-column: span 2;
            grid-row: 3/4;
            grid-column: 1/-1;
           
        }
        .slider-img{
            height: 100%;
            overflow: hidden;
            img{
               @extend%object-fit;
            }

        }
        
        .slider{
            max-width: 75.5rem;
            height: 100%;
            
            @media(max-width:100em){
                // max-width: 60rem;
            }
            @media(max-width:90em){
                // max-width: 60rem;
            }
        
            .slick-arrow{
                z-index: 10;
               bottom: 0;
               left: 50%;
               transform: translateX(-50%);
               display: none !important;
            }
        
            .slick-dots {
                text-align: center;
                left:0;
                bottom: 4rem !important;
                
                @media(max-width:100em){
                    bottom: 3rem !important;
                }
                @media(max-width:90em){
                    bottom: 3rem !important;
                }
                li button:before,
                li.slick-active button:before {
                  color: transparent;
                  opacity: 1;
                  margin: 5px;
                }
                li button:before{
                  background-color: transparent;
                  border: 1px solid #fff;
                  border-radius: 50%;
                  display: inline-block;
                  height: 15px;
                  width: 15px;
              
                }
                li.slick-active button:before {
                  background-color: #fff;
                }
        
                li{
                    margin-inline-end: 1.5rem;
                }
              }
              .slick-prev{
                  left: 2.5rem;
                  &::before{
                    content: "";
                    background-image: url(../img/arrow-white-slick.svg);
                    background-repeat: no-repeat;
                    background-size: contain;
                    display: inline-block;
                    width: 8px;
                    height: 16px;
                    transform: scale(-1);
                }
              }
              .slick-next{
                  left:15rem;
        
                  &::before{
                      content: "";
                      background-image: url(../img/arrow-white-slick.svg);
                      background-repeat: no-repeat;
                      background-size: contain;
                      display: inline-block;
                      width: 8px;
                      height: 16px;
                  }
              }
        }
    }
}

.activities-info{
    text-align: start;
    &__head{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 2.5rem;
        h3{
            font-size: 3.6rem;
            font-family: var(--font-medium);
            color: var(--color-black);
        }
        .icon-share{
            background-color: var(--color-light-blue);
            width: 4.4rem;
            height: 4.4rem;
            border-radius: 1.5rem;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            .icon{
                stroke: var(--color-white);
                width: 2.2rem;
                height: 1.8rem;
            }
        }
    }
    p{
        margin-bottom: 2.5rem;
        font-size: 1.5rem;
        @media(max-width:90em){
            font-size: 1.4rem;
        }
    }
    h4{
        font-size: 2.4rem;
        font-family: var(--font-medium);
        margin-bottom: 2.5rem;
        span{
            display: block;
            font-size: 2rem;
            font-family: var(--font-regular);
            color: #808080;
            margin-top: 7px;
            font-weight: normal;
        }
    }

    &__image{
        width: 100%;
        height: 25rem;
        margin-bottom: 2.5rem;
        img{
            @extend%object-fit;
        }
    }
}

.accordion-container{
    width: 100%;
    &__wrap-item{
        width: 100%;
        height: 22rem;
        border-radius: 2rem;
        overflow: hidden;
        position: relative;
        @media(max-width:48em){
            height: 28rem;
        }
        img{
            @extend%object-fit;
            }
        }

        .color-over{
            background-image: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0 7% , rgba(0,0,0,0) 20% 100%);
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            bottom: 0;
            z-index: 1;
        }
        h3{
            font-size: 2.4rem;
            font-family: var(--font-regular);
            text-transform: uppercase;
            position: absolute;
            z-index: 1;
            left: 2.4rem;
            bottom: 1.2rem;
            @media(max-width:90em){
                font-size: 2rem;
            }
        }

        .content-1{
            ul{
                margin: 0;
                li{
                    width: 100%;
                    background-color: var(--color-light-blue);
                    color: var(--color-white);
                    font-family: var(--font-regular);
                    font-size: 2rem;
                    border-radius: 1.6rem;
                    text-align: center;
                    margin: 0;
                    margin-bottom: 1rem;
                    line-height: 1;
                    &:hover{
                        background-color: var(--color-orange);
                    }
                    a{
                        color: var(--color-white);
                        margin: 0 !important;
                        padding: 1.5rem;
                        display: block;
                        text-transform: uppercase;
                    }
            }
        }
}
}