// -----------------------------------------------------------------------------
// This file contains all styles related to the footer of the site/application.
// -----------------------------------------------------------------------------

.footer{
    background-color: var(--color-black);
    padding: 6rem 4rem;
    height: 30rem;
    width: 100%;
    color: var(--color-white);
    @media(max-width:37.5em){
        padding: 4rem 2.7rem;
      }

    @media(max-width:37.5em){
        height: auto;
    }

    &__wrap{
        display: flex;
        flex-wrap: wrap;
        text-align: start;
        @media(max-width:37.5em){
          flex-direction: column;
          align-items: center;
        }
    }

    &__logo{
        @media(max-width:37.5em){
            order: 1;
            margin-bottom: 4rem;
        }
    }

    &__link{
        @media(max-width:37.5em){
            order: 4;
        }
    }

    &__item{
        flex: 1;
        &:not(:last-child){
            margin-inline-end: 1.5rem;
            @media(max-width:37.5em){
                margin-inline-end: 0;
            }
        }
        @media(max-width:37.5em){

            &:not(:last-child){
                margin-bottom: 4rem;
            }
            &:nth-child(2){
                margin-bottom: 0 !important;
            }
           
        }

        ul{
            margin-top: -.8rem;
            li{
                    line-height: 3;
                    font-size: 1.2rem;
                    
                   
                    @media(max-width:37.5em){
                        display: inline-flex;
                        &:first-child{
                            display: none;
                        }
                        &:not(:last-child){
                            &::after{
                                content: "";
                                display: inline-block;
                                width: 1px;
                                height: 5px;
                                background-color: var(--color-white);
                                margin: 0 3px;
                                transform: translate(2px, 11px);

                            }
                        }
                    }
                a{
                    color: var(--color-white);
                    line-height: 1.3;
                    &:hover{
                        color: var(--color-orange);
                    }
                }
            }
        }
    }

    &__follow{
        @media(max-width:37.5em){
            order: 3;
            text-align: center;
        }
        span{
            display: block;
            margin-bottom: 1.5rem;
            @media(max-width:37.5em){
                display: none;
            }

        }

        a{
            &:not(:last-child){
                margin-inline-end: 8px;
            }
        }

        .icon{
            height: 2.2rem;
            width: 2.2rem;
            fill: var(--color-white);
            @media(max-width:47em){
                fill: var(--color-light-blue);
                width: 30px;
                height: 30px;
            }
           
        }
    }

    &__download{
       @media(max-width:37.5em){
           order: 2;
           text-align: center;
       }
        span{
            display: block;
            margin-bottom: 1.5rem;
        }

        img{
            display: block;
            margin-bottom: 1.5rem;
        }

        &__wrap{
            display: inline-flex;
            align-items: flex-start;

            .app-store{
                margin-inline-end: 2rem;
            }

            .qr-code{
                display: none;
                @media(max-width:37.5em){
                    display: block;
                }
            }
        }
    }
}

.ministry{
    margin-top: 3rem;
    img{
        &:first-child{
            margin-inline-end: 3rem;
        }
    }
    @media(max-width:37.5em){
        display: none;
      }

      &--mobile{
          display: none;
        @media(max-width:37.5em){
           display: block;
           order:2;
           margin-top: 0;
           margin-bottom: 4rem;
          }
      }
}

.copyRight{
    @media(max-width:37.5em){
        display: none;
      }
    margin-top: 3rem;
    span{
        display: block;
        font-size: 1rem;
    }
}

.copy-mobile{
    @media(min-width:37.5em){
        display: none;
      }
      font-size: 10px;
      order: 4;
}