// -----------------------------------------------------------------------------
// This file contains very basic styles.
// -----------------------------------------------------------------------------

*{
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before{
  box-sizing:inherit;
}

html {
  font-size: 62.5%;
  box-sizing:border-box;
  @media(max-width:90em){
    font-size: 56.25%;
  }
  @media(max-width:48em){
    font-size: 50%;
  }

}

.page-wrapper{
  max-width: 120em;
  margin: 0 auto;
  overflow-x: hidden;
  overflow-y: auto;
  // padding-left: 2%;
  // padding-right: 2%;
  overflow-x: hidden;
  position: relative;
}

p{
  font-family: var(--font-book);
}

body {
  line-height: 1.4;
  overflow-x: hidden;
  font-family: var(--font-book);
  font-size: 1.6rem;
  height: 100vh;
  background-color: var(--color-white) !important;
  text-align: start;

  @media(min-width:37.5em){
    /* width */
::-webkit-scrollbar {
  width: 5px;
  opacity: .5;
  transform: translateX(-1rem);
  // display: none;
  position: absolute;
}

/* Track */
::-webkit-scrollbar-track {
  background: var(--color-drak-blue-1); 
  opacity: .5;
  display: none;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(23, 108, 148, 0.5); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--color-drak-blue); 
}
  }
}


.container{
 max-width: 150rem;
 padding: 0 4rem;

//  @media(max-width:93.75em){
//   max-width:120rem;
// }

//  @media(max-width:62em){
//    max-width: 100%;
//  }

 @media(min-width:120em){
  max-width: 150rem;
}

}

a,
a:link {
  text-decoration: none;
}

ul{
  list-style: none;
  padding: 0;
}



.select2-container{
  .select2-dropdown{
      background-color:var(--color-drak-blue-1);
      border: none;
      box-shadow: 2px 10px 10px #00000040;

  }
  .select2-results{
      ul{
          font-size: 1.6rem;
          

          .select2-results__option{
              padding: 0 1.5rem;
              line-height: 5rem;
              color: var(--color-white);
              background-color: var(--color-drak-blue-1);
              &[aria-selected="true"]{
                  background: var(--color-drak-blue-1);
                  color: white;
                  &.select2-results__option--highlighted{
                      background: var(--color-orange);
                  }
              }
          }
          .select2-results__option--highlighted[aria-selected]{
              background-color: var(--color-orange);
          }
      }
  }
}




// detail page


%pop-puAnim{
  transform: translateX(0) !important;
  
}

.popupVisible{
  @extend%pop-puAnim;
  visibility: visible !important;
}

.active-item{
  svg{
    stroke: var(--color-orange) !important;
  }
  span{
    color:  var(--color-orange) !important;
  }
}

.active-list{
  background-color: var(--color-orange) !important;
  color: var(--color-white) !important;
}

.slider-2{
  .slick-arrow{
    transform: translateY(200px) !important;
  }

  .slick-dots{
    bottom: 34.8rem !important;
  }
}

.gradent{
  width: 100%;
  height: 50vh;
  position: absolute;
  bottom: 0;
 
  &::after{
    content: "";
    display: block;
    background-image: linear-gradient(to top right, rgba(0, 0, 0, .8) 20%, rgba(0,0,0,0) 60% 100%);
    width: 50%;
    position: absolute;
    left: 0;
    bottom: 0;
    height: 100%;
    z-index:1;

  }

  &::before{
    content: "";
    display: block;
    background-image: linear-gradient(to top left, rgba(0, 0, 0, .8) 20%, rgba(0,0,0,0) 60% 100%);
    width: 50%;
    position: absolute;
    right: 0;
    bottom: 0;
    height: 100%;
    z-index:1;
    
  }

  
}

.playBtn-wrap{
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  border: 2px solid var(--color-white);
  display: flex;
  align-items: center;
  justify-content: center;
}

.play-btn {
	width: 1.5rem;
	height: 1.5rem;
	position: relative;
  margin: 5px;
  cursor: pointer;
	margin: auto;
	transform: rotate(-90deg) scale(0.8);
	transition: -webkit-clip-path 0.3s ease-in 0.1s, shape-inside 0.3s ease-in 0.1s,
		transform 0.8s cubic-bezier(0.85, -0.25, 0.25, 1.425);
}
.play-btn.paused {
	transform: rotate(0deg);
}
.play-btn:before,
.play-btn:after {
	content: "";
	position: absolute;
	background: white;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	transition: inherit;
}
.play-btn:before {
  clip-path: polygon(0 10%, 100% 10%, 100% 40%, 0 40%);
	-webkit-clip-path: polygon(0 10%, 100% 10%, 100% 40%, 0 40%);
}
.play-btn:after {
  clip-path: polygon(0 60%, 100% 60%, 100% 90%, 0 90%);
	-webkit-clip-path: polygon(0 60%, 100% 60%, 100% 90%, 0 90%);
}
.play-btn.paused:before {
  clip-path: polygon(10% 0, 90% 51%, 90% 51%, 10% 51%);
	-webkit-clip-path: polygon(10% 0, 90% 51%, 90% 51%, 10% 51%);
}
.play-btn.paused:after {
  clip-path: polygon(10% 49.5%, 80% 49.5%, 90% 49.5%, 10% 100%);
	-webkit-clip-path: polygon(10% 49.5%, 80% 49.5%, 90% 49.5%, 10% 100%);
}

.center-div{
  text-align: center;
}

.home-page.fp-viewing-fourthSection{
  .map-nav{
    display: none;
  }
}

.home-page{
  .header__middle{
    display: none;
  }
}
.animation-left{
  transform: translateX(0) !important;
  transition: all .3s ease-in-out !important;
  visibility: visible !important;
  
}


@keyframes favIconanim {
  0% {
    transform: scale(1);
    stroke-dasharray: 10;
    stroke-dashoffset: 0;
  }

  50%{
    transform: scale(1.15);
    stroke: var(--color-fav-icon);
    stroke-dasharray: 0;
    filter: drop-shadow(0 3px 6px var(--color-fav-icon));

    
    
  }
  100%{
    transform: scale(.9);
    fill:var(--color-fav-icon);
    stroke: var(--color-fav-icon);
    stroke-dasharray: 0;
  }

}

@keyframes fadeAnimpop {
  0%{
    opacity: 0;
  }

  100%{
    opacity: 1;
  }
}

.visible-notification{
  display: block !important;
  visibility: visible !important;
  animation: fadeAnimpop .5s ease-in ;
  animation-fill-mode: both;
}

