// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------

  
.header{
  background-color: var(--color-black);
  position: fixed;
  z-index: 99;
  width: 100%;
  top: 0;

  &__wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 9rem;
    padding: 0 4rem;
    @media(max-width:100em){
      height: 8rem;
    }
    @media(max-width:90em){
      height: 7rem;
    }
    @media(max-width:37.5em){
      height: 6.4rem;
      padding:  1.5rem;
    }
  }

  &__logo{
    @media(max-width:100em){
      height: 3rem;
    }
    @media(max-width:90em){
      height: 3rem;
    }
    @media(max-width:37.5em){
      max-width: 15.7rem;
      height: 3.6rem;
    }
  }

  &__middle{
      .region-title{
        display: inline-flex;
        align-items: center;
        text-transform: uppercase;
        cursor: pointer;
        @media(max-width:37.5em){
          display: none;
        }
        .icon{
          width: 9px;
          height: 18px;
          stroke: var(--color-orange);
          margin-inline-end: 12px;
          @media(max-width:100em){
            width: 7px;
            height: 15px;
          }
        }

        span{
          font-size: 2.4rem;
          font-family: var(--font-regular);
          color: var(--color-orange);
          letter-spacing: 3px;
          display: inline-block;
          line-height: 1;
          @media(max-width:100em){
            font-size: 2rem;
          }
          
        }
      }
  }

  &__end{
    display: inline-flex;
    align-items: center;

    .search {
      position: relative;
      height: 4rem;
      width: 4rem;
      @media(max-width:100em){
        width: 3.5rem;
        height: 3.5rem;
    }
    @media(max-width:37.5em){
        width: 3.5rem;
        height: 3.5rem;
      }
      .icon-search{
        width: 2.2rem;
        height: 2.2rem;
        @media(max-width:100em){
            width: 2.1rem;
            height: 2.1rem;
        }
    }
      .input {
        font-size: 2rem;
        width: 4rem;
        height: 4rem;
        margin: 0;
        border: 0;
        padding: 1rem;
        transition: width 0.3s ease;
        border-radius: 3rem;
        position: absolute;
        right: 0;
        @media(max-width:100em){
          width: 3.5rem;
          height: 3.5rem;
      }
      @media(max-width:37.5em){
         
          width: 3.5rem;
          height: 3.5rem;
        }
      
      }
      .btn {
        position: absolute;
        height: 4rem;
        width: 4rem;
        left: 0;
        border: 0;
        background-color: #fff;
        font-size: 2rem;
        cursor: pointer;
        border-radius: 3rem;
        transition: transform 0.3s ease;
        margin-top: 0;
        @media(max-width:100em){
          width: 3.5rem;
          height: 3.5rem;
      }
      @media(max-width:37.5em){
          
          width: 3.5rem;
          height: 3.5rem;
        }
      }
      .input:focus,
      .btn:focus {
        outline: none;
      }
    }
   
    .search.active .input {
      width: 50rem;
      right: 0;
    }
    .search.active .btn {
      
    }
    
  }
}


.menu-icon{
  cursor: pointer;
  @media(max-width:100em){
    transform: scale(.9);
  }
  @media(max-width:100em){
    transform: scale(.82);
  }
  @media(max-width:37.5em){
    transform: scale(.8);
  }
}
